// отступы входят в размер эл-тов
* {
    box-sizing: border-box; }

//добавляем шрифты
@font-face {
    font-family: "MontserratT";
    src: url('../fonts/Montserrat-Regular.ttf');
    font-weight: regular;
    font-style: normal; }
// сначала загружаются системные шрифты, потом встроенный для сайта
//  font-display: swap

@font-face {
    font-family: "MontserratT";
    src: url('../fonts/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal; }
// сначала загружаются системные шрифты, потом встроенный для сайта
//  font-display: swap

html {
    font-family: "MontserratT";
    font-weight: regular; }


$color-blue: #134b9e;
$color-orange: #F28B64;
$color-purple: #D3ACC3;
$color-dark-purple: #BC2F90;
$color-background: #E5E5E5;

.container {
    max-width: 1140px;
    margin: 0 auto;
    position: relative; }

.main-section {
    min-height: 800px;
    background-color: $color-background;
    padding-top: 16px;
    // обрезаем границы круга за секцией
    overflow: hidden;
    position: relative;
    &__nav-container {
        display: flex;
        // flex-direction: row
        justify-content: space-between;
        height: 69px;
        align-items: center; }
    &__logo {
        width: 134px;
        transition: 0.7s; }
    &__logo:hover {
        transform: scale(1.2); }
    &__content-container {
        display: flex;
        position: relative; }
    &__first-ellipse {
        position: absolute;
        top: 100px;
        right: 6px; }
    &__second-ellipse {
        position: absolute;
        top: 650px;
        right: -120px; } }

.nav-menu {
    &__list {
        display: flex;
        // flex-direction: row
        justify-content: space-between;
        width: 363px; }
    a {
        text-decoration: none;
        color: $color-blue;
        font-size: 20px;
        font-weight: bold;
        transition: 0.7s; }
    a:hover {
        color: $color-dark-purple; }
    &__list-item {
        // margin-right: 64px
        // font-size: 18px
        // &:last-child
 } }        //  // margin-right: 0

.left-wrapper {
    position: relative;
    img {
        position: relative;
        z-index: 5; }
    &__rectangle-blue {
        position: absolute;
        top: 112px;
        left: -60px;
        height: 235px;
        width: 596px;
        background: $color-blue;
        border-radius: 100px;
        // тень право-лево, верх-низ, радиус, цвет
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-64deg);
        z-index: 3; }
    &__rectangle-orange {
        position: absolute;
        width: 615px;
        height: 220px;
        left: -20px;
        top: 135px;
        background: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-50.81deg);
        z-index: 2; }
    &__rectangle-purple {
        position: absolute;
        width: 608.97px;
        height: 220px;
        left: 10px;
        top: 165px;
        background-color: $color-purple;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-36.15deg);
        z-index: 1; } }

.right-wrapper {
    width: 372px;
    height: 320px;
    position: absolute;
    top: 70px;
    right: 76px;
    z-index: 6;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__text {
        font-size: 16px;
        line-height: 1.1; } }

.icons {
    display: flex;
    width: 372px;
    justify-content: space-between;
    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.7s;
        // width: 40px
        // height: 40px
        // border: 1px solid $color-blue
        // border-radius: 50%
        // img
        //     width: 21px
 }        //     height: 21px
    &__item:hover {
        transform: scale(1.2); } }

.title {
    font-size: 72px;
    position: relative;
    font-weight: bold;
    &__hr {
        width: 65px;
        position: absolute;
        bottom: 0px;
        margin: 0;
        border: 0;
        border-top: 1px solid #000000; } }

.buttons {
    display: flex;
    &__main-btn {
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background:  $color-dark-purple;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        border: 0;
        color: white;
        font-size: 18px;
        font-weight: bold;
        transition: 0.7s; }
    &__main-btn:hover {
        background: white;
        color: $color-dark-purple;
        border: 1px solid $color-dark-purple; }
    &__icon-btn {
        display: flex;
        align-items: center;
        img {
            margin-right: 12px; } }
    &__text {
        font-size: 16px; } }

.ellipse {
    width: 194px;
    height: 194px;
    background: linear-gradient(143.92deg, rgba(237, 123, 80, 0.5) 14.85%, rgba(72, 138, 234, 0.5) 53.25%, rgba(44, 44, 44, 0.5) 85.74%);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    &_big {
        width: 282px;
        height: 282px; }
    &__subtract {
        width: 129.33px;
        height: 129.33px;
        background: $color-background;
        border-radius: 50%;
        &_big {
            width: 188px;
            height: 188px;
            background:  $color-background;
            border-radius: 50%; }
        &_white {
            background: white; } } }

.second-section {
    min-height: 700px;
    overflow: hidden;
    &__container {
        padding-top: 45px;
        position: relative; }
    &__title {
        margin-bottom: 40px; }
    &__cards-box {
        display: flex;
        justify-content: space-between;
        margin-bottom: 55px; }
    &__icons-box {
        display: flex;
        justify-content: space-evenly; }
    &__first-ellipse {
        position: absolute;
        top: 80px;
        right: -60px;
        z-index: -1; }
    &__second-ellipse {
        position: absolute;
        top: 364px;
        left: -60px;
        z-index: -1; } }

.cards-item {
    position: relative;
    width: 360px;
    height: 360px;
    background: $color-purple;
    border-radius: 10px;
    display: flex;
    align-items: flex-end;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    &__img {
        height: 335px; }
    &__text {
        width: 280px;
        font-size: 36px;
        font-weight: bold;
        text-align: center;
        transform: rotate(90deg);
        position: absolute;
        top: 144px;
        right: -44px; }
    &_orange {
        background: $color-orange; }
    &_blue {
        background: $color-blue; } }

.icons-item {
    display: inline-flex;
    align-items: center;
    &__img {
        margin-right: 20px;
        width: 80px; }
    &__text {
        font-size: 18px;
        span {
            font-size: 48px;
            font-weight: bold; } } }

.third-section {
    min-height: 650px;
    background: $color-background;
    position: relative;
    z-index: 0;
    overflow: hidden;
    &__container {
        display: flex;
        align-items: center;
        padding-top: 40px;
        justify-content: space-evenly; }
    &__right-wrapper {
 }        // max-width: 480px
    &__right-title {
        margin-bottom: 20px; }
    &__text {
        margin-bottom: 30px;
        line-height: 1.1;
        font-size: 18px;
        width: 390px; }
    &__first-ellipse {
        position: absolute;
        top: 190px;
        left: 110px;
        z-index: -1; }
    &__figure {
        position: absolute;
        top: 305px;
        left: 15px;
        z-index: -1; }
    &__second-ellipse {
        position: absolute;
        right: -120px;
        top: -120px; } }

.title__hr_top {
    bottom: 75px; }

.footer {
    &__img {
        position: absolute;
        left: -250px;
        bottom: 0; }
    &__container {
        min-height: 272px;
        display: flex;
        align-items: center;
        justify-content: flex-end; }
    &__title {
        margin-bottom: 20px; } }

// АДАПТИВ

// Мал комп или горизонтальный планшет
@media (max-width: 1199px) {
    .container {
        width: 960px; }
    .main-section {
        min-height: 690px;
        &__second-ellipse {
            top: 550px;
            right: -105px; } }
    .nav-menu__list {
        width: 385px; }
    .left-wrapper {
        img {
            width: 750px; }
        &__rectangle-blue {
            width: 501px;
            height: 235px;
            left: -45px;
            top: 74px; }
        &__rectangle-orange {
            width: 512px;
            height: 224px;
            left: -12px;
            top: 89px; }
        &__rectangle-purple {
            width: 479.97px;
            height: 210px;
            left: 48px;
            top: 105px; } }
    .right-wrapper {
        top: 35px;
        right: 10px; }
    .main-section__first-ellipse {
        top: 65px;
        right: -44px; }

    .second-section {
        min-height: 620px;
        &__cards-box {
            margin-bottom: 45px; } }
    .cards-item {
        width: 300px;
        height: 300px;
        &__img {
            height: 290px; }
        &__text {
            font-size: 30px;
            top: 132px;
            right: -60px; } }
    .icons-item__img {
        margin-right: 10px; }
    span {
        font-size: 40px; }

    .third-section {
        min-height: 575px;
        &__container {
            padding-top: 30px; }
        &__text {
            width: 460px; }
        &__first-ellipse {
            top: 155px;
            left: 85px; }
        &__figure {
            top: 310px;
            left: 30px; } }

    .footer {
        &__container {
            min-height: 245px; }
        &__img {
            width: 600px;
            left: -250px;
            top: -15px; }
        &__title {
            font-size: 65px; } }
    .title__hr_top {
        bottom: 60px; } }

