        width: 720px; }

    .ellipse {
        width: 150px;
        height: 150px;
        &__subtract {
            width: 115px;
            height: 115px;
            &_big {
                width: 100px;
                height: 100px; } } }

    .main-section {
        min-height: 525px;
        &__first-ellipse {
            top: 37px;
            right: -82px; }
        &__second-ellipse {
            top: 435px;
            right: -35px; } }
    .left-wrapper {
        img {
            width: 550px; }
        &__rectangle-blue {
            width: 360px;
            height: 175px;
            left: -21px;
            top: 47px; }
        &__rectangle-orange {
            width: 360px;
            height: 175px;
            left: 10px;
            top: 60px; }
        &__rectangle-purple {
            width: 360px;
            height: 175px;
            left: 30px;
            top: 70px; } }
    .right-wrapper {
        top: 10px;
        right: -75px;
        height: 257px; }

    .title {
        font-size: 50px; }
    .icons {
        width: 300px; }
    .buttons__main-btn {
        width: 170px;
        height: 55px;
        font-size: 16px; }

    .second-section {
        min-height: 440px;
        &__container {
            padding-top: 15px; }
        &__title {
            margin-bottom: 15px; }
        &__first-ellipse {
            top: 27px;
            right: -22px; }
        &__second-ellipse {
            top: 190px;
            left: -23px; } }
    .cards-item {
        width: 220px;
        height: 220px;
        &__img {
            height: 215px; }
        &__text {
            font-size: 25px;
            top: 85px;
            right: -85px; } }

    .icons-item {
        &__img {
            width: 55px; }
        &__text {
            font-size: 15px; }
        span {
            font-size: 30px; } }

    .third-section {
        min-height: 470px;
        &__container {
            padding-top: 20px; }
        &__text {
            width: 392px;
            font-size: 15px; }
        &__first-ellipse {
            top: 122px;
            left: 22px; }
        &__figure {
            width: 110px;
            top: 183px;
            left: -7px; }
        &__second-ellipse {
            right: -51px;
            top: -45px; } }
    .title__hr_top {
        bottom: 50px; }

    .footer {
        &__container {
            min-height: 195px; }
        &__img {
            width: 457px;
            left: -181px;
            top: -7px; } } }

// Горизонтальный тлф
@media (max-width: 767px) {
    .container {
        width: 540px; }
    .icons {
        width: 260px; }
    .main-section {
        min-height: 407px;
        &__logo {
            width: 90px; }
        &__first-ellipse {
            display: none; }
        &__second-ellipse {
            top: 380px; }
        &__buttons {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            margin-left: 100px;
            height: 130px;
            align-items: center; } }

    .left-wrapper {
        img {
            width: 440px;
            height: 370px;
            top: 2px;
            left: -22px; }
        &__rectangle-blue {
            width: 260px;
            height: 120px;
            left: -10px;
            top: 20px; }
        &__rectangle-orange {
            width: 260px;
            height: 120px;
            left: 10px;
            top: 20px; }
        &__rectangle-purple {
            width: 260px;
            height: 120px;
            left: 20px;
            top: 25px; } }
    .right-wrapper {
            top: -20px;
            right: -17px;
            width: 240px;
            height: 350px;
            align-items: center;
            text-align: justify; }


    .title {
        font-size: 40px; }

    .nav-menu {
        display: flex;
        position: fixed;
        top: 0;
        right: -100%;
        height: 100vh;
        width: 50%;
        background: rgba(153, 174, 204, 0.9);
        z-index: 10;
        transition: 0.7s all;
        &__list {
            justify-content: space-evenly;
            flex-direction: column;
            align-items: center; }
        a {
            font-size: 25px; } }
    .hamburger {
        position: fixed;
        top: 40px;
        right: 40px;
        width: 30px;
        height: 15px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        z-index: 20;
        span {
            height: 2px;
            width: 100%;
            background: black;
            transition: 1s all;
            display: block; } }
    .hamburger_active {
        height: 4px;
        span {
            &:nth-child(1) {
                transform: translateY(5px) rotate(-45deg); }
            &:nth-child(2) {
                display: none; }
            &:nth-child(3) {
                transform: translateY(3px) rotate(45deg); } } }
    .nav-menu_active {
        right: 0px; }

    .second-section {
        min-height: 1050px;
        &__cards {
            min-height: 910px;
            display: flex;
            flex-direction: column;
            align-items: center; }
        &__cards-box {
            flex-direction: column;
            align-items: center;
            height: 800px;
            margin-bottom: 20px; }
        &__icons-box {
            display: flex;
            justify-content: space-between;
            width: 400px;
            height: 150px;
            flex-wrap: wrap; }
        &__container {
            padding-top: 15px; }
        &__first-ellipse {
            top: 285px;
            right: -70px; }
        &__second-ellipse {
            top: 555px;
            left: -80px; } }
    .cards-item {
        width: 500px;
        height: 250px;
        &__img {
            height: 245px; }
        &__text {
            transform: none;
            font-size: 35px;
            line-height: 1.5;
            top: 70px;
            right: 60px; } }
    .icons-item {
        &__img {
            width: 55px; }
        span {
            font-size: 20px; } }
    .media767 {
        margin-right: 19px; }

    .third-section {
        min-height: 525px;
        &__left-wrapper {
            display: none; }
        &__text {
            width: 500px;
            font-size: 20px; }
        &__buttons {
            width: 480px;
            display: flex;
            justify-content: space-between; } }

    .title__hr_top {
        bottom: 40px; }

    .footer {
        &__container {
            min-height: 160px;
            padding-top: 7px; }
        &__img {
            width: 375px; } } }

// Вертикальный тлф
@media (max-width: 575px) {
    .container {
        width: 100%; } }






